
#message {
    height: 250px; /* Высота поля в пикселах */
    resize: none; /* Запрещаем изменять размер */
} 

.help-block {
    display: block;
    margin-top: 5px;
    margin-bottom: 10px;
    color: $brand-third-color;
}

.text-danger {
    color: $brand-third-color;
    text-align: left;
}

.form-control {
    display: block;
    width: 100%;
    padding: 6px 12px;
    font-family: $font-family-base;
    font-size: $font-size-base;
    line-height: 1.6;
    color: $gray;
    box-shadow: none;
    transition: none;
    border: 1px solid black;
    /*  margin-bottom: 15px; */
   
}

.form-control:focus {
    border-color: $brand-color;
    outline: 0;
    -webkit-box-shadow: transparent;
    box-shadow: transparent;
}

.alert-success {
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
}

.alert {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px;
    font-size: 14pt;
}



/* 
social-list */

.social {
    margin-top: 40px;
}

.social ul li {
    display: inline-block;
    margin: 0 20px;
}

.social i.fa {
    font-size: 30px;
    padding: 4px 5px;
    color: #f4f5f6;
    transition: all 0.5s;
}

.social i.fa:hover {
    color: #E87E04;
}
