@import "variables";

@font-face {
    font-family: 'icomoon';
    src:    url('#{$icomoon-font-path}/icomoon.eot?jc3iwa');
    src:    url('#{$icomoon-font-path}/icomoon.eot?jc3iwa#iefix') format('embedded-opentype'),
        url('#{$icomoon-font-path}/icomoon.ttf?jc3iwa') format('truetype'),
        url('#{$icomoon-font-path}/icomoon.woff?jc3iwa') format('woff'),
        url('#{$icomoon-font-path}/icomoon.svg?jc3iwa#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-book {
    &:before {
        content: $icon-book;
    }
}
.icon-file-text {
    &:before {
        content: $icon-file-text;
    }
}
.icon-user-tie {
    &:before {
        content: $icon-user-tie;
    }
}
.icon-hammer {
    &:before {
        content: $icon-hammer;
    }
}
.icon-github {
    &:before {
        content: $icon-github;
    }
}
.icon-linkedin {
    &:before {
        content: $icon-linkedin;
    }
}
.icon-vk {
    &:before {
        content: $icon-vk;
    }
}
.icon-skype {
    &:before {
        content: $icon-skype;
    }
}

