/* @import 'icons/variables'; */
.header {
    .mainheader {
        background: #000;
        padding: 4px 0;
        justify-content: flex-end;
        .nav {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            justify-content: center;
            margin: 0;
            padding: 0;
            list-style: none;
            li {
                &:hover {
                    background-color: $brand-third-color;
                }
                margin-top: 10px;
                a {
                    display: block;
                    text-transform: uppercase;
                    font-size: 14px;
                    color: #fff;
                    text-decoration: none;
                    padding: 10px;
                    margin-right: 10px;
                    margin-left: 10px;


                }
            }
        }

        @media screen and (max-width: 768px) {
            padding: 11px 0 13px;
            .nav {
                li {
                    a {
                        font-size: 8pt;
                       
                    }
                   
                }
            }
        }

 }
}

