.timeline {
    position: relative;
    padding: 0;
    list-style: none;
    line-height: $line-height-base;
    &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 30px;
        width: 2px;
        margin-left: -1.5px;
        background-color: #eee;
    }
    >li {
        position: relative;
        margin-bottom: 10px;
        min-height: 10px;
        &:before {
            content: " ";
            display: table;
        }
        &:after {
            content: " ";
            display: table;
            clear: both;
        }
        .timeline-panel {
            float: left;
            position: relative;
            width: 100%;
            padding: 10px 10px 0 80px;
            text-align: left;
            &:before {
                right: auto;
                left: -20px;
                border-right-width: 20px;
                border-left-width: 0;
            }
            &:after {
                right: auto;
                left: -24px;
                border-right-width: 24px;
                border-left-width: 0;
            }
        }
        .timeline-image {
            z-index: 100;
            position: absolute;
            left: -5px;
            width: 60px;
            height: 60px;
            margin-left: 0;
            border: 1px solid #eee;
            border-radius: 100%;
            text-align: left;
            background: #fff;
            h4 {
                margin-top: 20px;
                margin-left: 12px;
                font-size: 9pt;
                font-weight: normal;
                text-transform: uppercase;
            }
        }
        .timeline-inverted {
            >.timeline-panel {
                float: left;
                position: relative;
                width: 100%;
                padding: 0px 10px 0 80px;
                text-align: left;
                &:before {
                    right: auto;
                    left: -15px;
                    border-right-width: 15px;
                    border-left-width: 0;
                }
                &:after {
                    right: auto;
                    left: -14px;
                    border-right-width: 14px;
                    border-left-width: 0;
                }
            }
        }
        &:last-child {
            margin-bottom: 10px;
        }
    }
    .timeline-heading {
        >h4 {
            margin-top: 0;
            text-transform: uppercase;
            font-size: $font-size-h4;
            line-height: 1.2;
        }
        .subheading {
            text-transform: none;
            color: $brand-third-color;
            margin-bottom: 5px;
        }
        >h2 {
            margin-top: 5px;
        }
    }
    .timeline-body >p,
    ul {
        margin-bottom: 0;
        color: #000;
        font-size: 12pt;
    }
}


@media(min-width:768px) {
    .timeline {
        &:before {
            left: 50%;
        }
        >li {
            margin-bottom: 50px;
            min-height: 50px;
            .timeline-panel {
                float: left;
                width: 51%;
                padding: 0 50px 10px 0;
                text-align: right;
            }
            .timeline-image {
                left: 55%;
                width: 70px;
                height: 70px;
                margin-left: -50px;
                > h4 {
                    margin-top: 20px;
                    margin-left: 20px;
                    line-height: 1.2;
                    font-weight: 400;
                }
            }
        }
        .timeline-inverted {
            >.timeline-panel {
                float: right;
                padding: 0 0 20px 60px;
                text-align: left;
                width: 51%;
            }
        }
    }
}

@media(min-width:992px) {
    .timeline {
        >li {
            min-height: 100px;
            .timeline-panel {
                float: left;
                text-align: right;
            }
            .timeline-image {
                width: 70px;
                height: 70px;
                margin-left: -60px;
                >h4 {
                    margin-top: 20px;
                    font-size: $font-size-h4;
                    line-height: 1.3;
                }
            }
        }
        .timeline-inverted {
            >.timeline-panel {
                float: right;
                padding: 0 20px 20px 180px;
                text-align: left;
                width: 81%;
            }
        }
    }
}

@media(min-width:1200px) {
    .timeline {
        >li {
            min-height: 70px;
            .timeline-image {
                width: 70px;
                height: 70px;
                margin-left: -60px;
                margin-top: 5px;
                > h4 {
                    margin-top: 20px;
                    text-align: left;
                    margin-left: 18px;
                }
            }
        }
    }
}

@media(min-width:1320px) {
    .timeline {
        >li {
            .timeline-panel {
                padding: 10px 70px 20px 10px;
            }
            
            .timeline-image {
                width: 80px;
                height: 80px;
                margin-left: -70px;
                margin-top: 5px;
                >h4 {
                    margin-top: 23px;
                    margin-left: 22px;
                }
            }
        }
        .timeline-inverted {
                >.timeline-panel {
                    float: right;
                    padding: 0 20px 20px 260px;
                    text-align: left;
                }
            }
    }
}
