.logo {
    margin-left: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
    max-width: 100px;
    position: relative;
}

.block {
    padding: 46px 0 30px;
    &-intro {
        text-align: left;
        padding-top: 0px;
        position: relative;
        width: 100%;
        height: auto;
        overflow: inherit;
        .main-img {
            max-width: 100%;
            height: auto;
            position: relative;
        }
    
    }
    &-resume {
        text-align: center;
        line-height: $line-height-base;
        .icon {
            color: $brand-third-color;
            font-size: $font-size-h1;
        }
        p {
            font-size: $font-size-base;
           
        }
        .education {
            background-color: $brand-color;
            height: auto;
            color: #fff;
            text-align: center;
        }
        .sertificates {
            text-align: left;
            color: #000;
            padding: 15px;
            margin-top: 20px;
        }
        .skills {
            text-align: left;
            vertical-align: middle;
            >ul>li {
                color: $brand-secondary-color;
                font-size: $font-size-base;
                font-weight: 700;
                text-transform: uppercase;
                line-height:  $line-height-base;
            }
        }
        .experience {
          
        }
        
    }
    &-projects {}
    &-contacts {
        text-align: center;
        margin-left: 120px;
        margin-right: 120px;
    }
}


