.page-header {
   
    text-align: center;
    padding: 10px;
    position: relative;
    color: $brand-color;
 
    
   /*  &:hover {
       &:before {
           content: '';
           position: absolute;
           left: 0;
           border-top: 100px solid $brand-third-color;
           border-right: 1000px solid transparent;
           cursor: pointer;
       }
   } */
    h1,
    .h1 {
        margin: 5px;
        padding-top: 5px;
        display: inline-block;
        font-size: $font-size-h1;
        text-transform: uppercase;
        color: $brand-color;
       
        &:after {
            left: 100%;
        }
        &:before {
            right: 100%;
        }
    }
    &-line{
    height: 4px;
    width: 70px;
    text-align: center;
    position: relative;
    background: $brand-third-color;
    margin: 0 auto;
    border: 0;
    }
    @media screen and (min-width: 480px) {
        margin-bottom: 30px; 
        h1,
        .h1 {
            font-size: $font-size-h3;
        }
    }
    @media screen and (min-width: 769px) {
        h1,
        .h1 {
            font-size: $font-size-h3;
           /*  padding: 0 95px; */
        }
    }
}
