@import "icons/style";
.list {
    &-social {
        margin: 0;
        padding: 0;
        text-align: right;
        >ul>li {
            margin-right: 20px;
            display: inline;
            list-style: none;
            >a {
                color: #fff;
                font-size: 25px;
                text-decoration: none;
                &:hover{
                	color:$brand-third-color;
                }
            }
        }
        @media screen and (min-width: 480px) {}
    }
}

.rounded {
    counter-reset: li;
    list-style: none;
    font: 14px;
    padding: 0;
    text-shadow: 0 1px 0 rgba(255, 255, 255, .5);
}

.rounded a {
    position: relative;
    display: block;
    padding: 15px 15px;
    margin: 15px;
    background: $brand-third-color;
    color: #fff;
    text-decoration: none;
    border-radius: 3px;
    transition: all .3s ease-out;
}


/* .rounded a:hover {
background: $brand-secondary-color;
} */

.rounded a:before {
    content: counter(li);
    counter-increment: li;
    position: absolute;
    left: -30px;
    top: 50%;
    margin-top: -20px;
    background: $brand-third-color;
    height: 40px;
    width: 40px;
    line-height: 30px;
    border: .3em solid white;
    text-align: center;
    font-weight: bold;
    border-radius: 40px;
    transition: all .3s ease-out;
}

.bullet {
    margin-left: 0;
    list-style: none;
    margin-top: 50px;
    /* counter-reset: li; */
}

.bullet li {
    position: relative;
    margin-bottom: 27px;
    border: 1px solid $brand-color;
    padding: 0.6em;
    border-radius: 3px;
    color: #000;
}

.bullet li:before {
    position: absolute;
    top: -0.7em;
    padding-left: 0.4em;
    padding-right: 0.4em;
    font-size: 16px;
    font-weight: bold;
    color: $brand-third-color;
    background: #fff;
    /* 
counter-increment: li;
content: counter(li); */
}

.years {
    position: absolute;
    margin-top: -180px;
    margin-left: 280px;
    color: $brand-color;
    >li {
        background-color: #fff;
        margin-bottom: 60px;
    }
}
